<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn 
        v-on="on"
      	icon 
      	color="primary">
				<v-icon small>mdi-pencil</v-icon>
			</v-btn>
    </template>

    <v-card>
      <v-card-title class="py-2">
        Edit Bidang
      </v-card-title>
      <v-divider></v-divider>
      <v-form v-model="valid" ref="form">
        <v-card-text class="pa-6 pb-0">
          <v-text-field
            v-model="data.nama"
            label="Nama"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-text-field
            v-model="data.point.value"
            label="Point"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-select
            v-model="detailTingkat"
            :items="dataTingkat"
            item-text="nama"
            item-value="id"
            label="Tingkat"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-select>

          <Notif :msg="msg" :status="status" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            color="primary"
            :loading="isLoading"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

  export default {
  	props: ['data'],

    data () {
      return {
        dialog: false,
        valid: true,
        requiredRule: [v => !!v || 'Data harus diisi'],        
        msg: {
          success: 'Data berhasil diedit',
          error: 'Data gagal diedit',
          visible: false
        }
      }
    },

    components: { Notif },

    created() {
      this.data.point.value = parseInt(this.data.point.value)
    },

    computed: { 
      ...mapState('dataBidang', ['status', 'isLoading']),
      ...mapState('dataTingkat', ['dataTingkat']),
      detailTingkat: {
        get() {
          const hasil = this.dataTingkat.filter(tingkat => 
            tingkat.nama === this.data.tingkat)
          return {...hasil[0]}
        },

        set(val) {
          // this.dataTingkat.filter(tingkat => 
          //   tingkat.id === val)
          this.data.tingkat_id = val
        }
      }
    },

    watch: {
      'data.tingkat': function (val) {
        this.detailTingkat = val
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          this.$store.dispatch('dataBidang/putDataBidang', {
            id: this.data.id,
            nama: this.data.nama,
            point: this.data.point.value,
            tingkat_id: this.data.tingkat_id
          })
          this.$store.dispatch('dataBidang/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
      }
    }
  }
</script>
