<template>
	<div>
		<TambahBidang/>
		<v-form ref="form" v-model="valid" class="mt-6">
			<v-row dense>
				<v-col cols="12" md="4" sm="6" class="py-0">
					<v-autocomplete
						label="Bidang"
						outlined
						dense
						v-model="dataFilter.bidang"
						:items="data"
						required
						:rules="requiredRule"
					></v-autocomplete>
				</v-col>
				<v-col cols="12" md="4" sm="6" class="py-0">
					<v-autocomplete
						label="Tingkat"
						outlined
						dense
						v-model="dataFilter.tingkat"
						:items="dataTingkat"
						item-value="id"
						item-text="nama"
						required
						:rules="requiredRule"
					></v-autocomplete>
				</v-col>
				<v-col cols="12" md="4" sm="6" class="py-0">
					<v-btn
						type="submit"
						:disabled="!valid"
						@click.prevent="validate"
						color="primary"
						depressed>
						filter
					</v-btn>
				</v-col>
			</v-row>
		</v-form>
		<DataBidang/>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import DataBidang from '@/components/Prestasi/Bidang/DataBidang'
	import TambahBidang from '@/components/Prestasi/Bidang/TambahBidang'

	export default{
		data: () => ({
			valid: true,
			requiredRule: [v => !!v || 'Data harus diisi'],
			data: ['foo'],
			dataFilter: {
				bidang: '',
				tingkat: ''
			}
		}),

		components: {
			TambahBidang,
			DataBidang
		},

		created() {
			this.$store.dispatch('dataTingkat/getDataTingkat')
		},

		computed: { ...mapState('dataTingkat', ['dataTingkat']) },

		methods: {
			validate () {
				if (this.$refs.form.validate()) {
					console.log(this.dataFilter)
				}
			}
		}
	}
</script>