<template>
	<div>
		<Loader v-if="isLoading" />
		<v-data-table
			v-else
			class="mt-0"
			:headers="header"
			:items="dataBidang"
			:items-per-page="10">
			<template v-slot:no-data>
	      <v-alert type="info" class="mt-4">
	        <div>Data tidak ditemukan</div>
	      </v-alert>
	    </template>
	    <template v-slot:item.no="{item}">
	    	{{dataBidang.map(x => x.id).indexOf(item.id)+1}}    	
	    </template>
	    <template v-slot:item.point="{item}">
	    	{{ parseInt(item.point.value)+'%' }}
	    </template>
			<template v-slot:item.action="{item}">
				<v-btn icon color="success">
					<v-icon small>mdi-eye</v-icon>
				</v-btn>
				<EditBidang :data="item" />
				<HapusBidang :id="item.id" />
			</template>		
		</v-data-table>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import EditBidang from '@/components/Prestasi/Bidang/EditBidang'
	import HapusBidang from '@/components/Prestasi/Bidang/HapusBidang'
	import Loader from '@/components/Loader'

	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Tingkat', value: 'tingkat', sortable: false },
				{ text: 'Bidang', value: 'nama', sortable: false },
				{ text: 'Poin', value: 'point', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			],

			data: [
				{
					tingkat: {
						id: 1,
						nama: 'SMP'
					},
					nama: 'IPA',
					point: 100,
					id: 1
				}
			],
		}),

		components: {
			Loader,
			HapusBidang,
			EditBidang
		},

		created() {
			this.$store.dispatch('dataBidang/getDataBidang')
			this.$store.dispatch('dataBidang/updateIsLoading', true)
		},

		computed: { ...mapState('dataBidang', ['dataBidang', 'isLoading']) }
	}
</script>